.main-section {
    display: flex;
    flex-direction: column;
    width: 1440px;
    height: 705px;
    margin: 0 auto;
    margin-top: 30px;
    background: {}
    &__search {
        display: flex;
        flex-direction: row;
        width: 1300px;
        margin: 0 auto;
        margin-bottom: 20px;
        font-size: 15px; }
    &__table {
        width: 1300px;
        margin: 0 auto;
        margin-bottom: 20px; }
    &__navigation {
        display: flex;
        justify-content: center;
        margin: 0 auto; } }

.form-search {
    width: 1300px;
    height: 35px;
    display: flex;
    flex-direction: row; }

input {
    width: 90%;
    margin-right: 10px;
    background: rgba(44, 44, 44, 0.3);
    border: none;
    outline: none;
    padding: 7px;
    font-size: 13px;
    color: #fff;
    text-shadow: 1px 1px 1px rgba(0,0,0,0.3);
    border: 1px solid rgba(0,0,0,0.3);
    border-radius: 4px;
    box-shadow: inset 0 -5px 45px rgba(192, 192, 192, 0.2), 0 1px 1px rgba(255, 255, 255, 0.471);
    -webkit-transition: box-shadow .5s ease;
    -moz-transition: box-shadow .5s ease;
    -o-transition: box-shadow .5s ease;
    -ms-transition: box-shadow .5s ease;
    transition: box-shadow .5s ease; }

// input:focus
//     box-shadow: inset 0 -5px 45px rgba(100,100,100,0.4), 0 1px 1px rgba(255,255,255,0.2)

a {
    color: #b55800;
    text-decoration: underline; }

table {
    width: 1300px;
    margin: 0 auto; }

small {
    font-size: 15px;
    font-weight: bold; }

th {
    text-justify: center;
    height: 30px;
    background-color: #b1bac0; }
tr {
    &:nth-child(even) {
        background-color: #b1bac0; } }

.btn-primary {
    --bs-btn-color: #fff;
    --bs-btn-bg: #d86900;
    --bs-btn-border-color: #d86900;
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: #af5c0e;
    --bs-btn-hover-border-color: #c76000;
    --bs-btn-focus-shadow-rgb: 49,132,253;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: #c76000;
    --bs-btn-active-border-color: #c76000;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #fff;
    --bs-btn-disabled-bg: #a3652b;
    --bs-btn-disabled-border-color: #8a5e34; }

.btn {
    text-align: center;
 }    // --bs-btn-font-size: 15px

.dismiss {
    background-color: #d83600;
    border-color: #af2d02;
    border: none;
    border-radius: 5px;
    color: white;
    // padding: 15px 32px
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin: 4px 2px;
    // cursor: pointer
    font-size: 15px; }
